.etc-indicator {
  background-color: #4c4c4c;
  border-radius: 5px;
  position: fixed;
  text-align: center;
  padding: .6rem;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  overflow: hidden;
  z-index: 4000;
}
.etc-indicator .title {
    color: white;
    margin-top: .3rem;
    word-wrap: normal;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    text-align: center;
}
.etc-indicator-mask {
    background-color: transparent;
    position: fixed;
    z-index: 2000;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.etc-indicator .circle {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    border: 1px solid #e6e6e6;
    border-bottom-color: transparent;
    margin: 0 auto;
}
.etc-indicator .circle-rotate {
    -webkit-animation: circleRotate 0.6s linear infinite;
    animation: circleRotate 0.6s linear infinite;
}
@-webkit-keyframes circleRotate {
0% {
    -webkit-transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
}
}
@keyframes circleRotate {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}



