
@charset "UTF-8";
/*列表*/
.mescroll {
  position: fixed;
  top: 0px;
  bottom: 0;
  height: auto;
  background: #fff;
}

/*展示上拉加载的数据列表*/
.data-list li {
  position: relative;
  padding: 10px 8px 10px 120px;
  border-bottom: 1px solid #eee;
}
.data-list .pd-img {
  position: absolute;
  left: 18px;
  top: 18px;
  width: 80px;
  height: 80px;
}
.data-list .pd-name {
  font-size: 16px;
  line-height: 20px;
  height: 40px;
  overflow: hidden;
}
.data-list .pd-price {
  margin-top: 8px;
  color: red;
}
.data-list .pd-sold {
  font-size: 12px;
  margin-top: 8px;
  color: gray;
}
.btn-empty {
  margin-top: 1rem;
}



