.etc-toast {
  background-color: #4c4c4c;
  width: 7rem;
  padding: 0.53125rem;
  border-radius: 4px;
  text-align: center;
  position: fixed;
  left: 50%;
  top: 50%;
  margin-left: -3.5rem;
  margin-top: -6.25rem;
  overflow: hidden;
  z-index: 4000;
}
.etc-toast .title {
    color: white;
}
.etc-toast .icon {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    margin: 0.625rem auto;
    padding: 0;
    position: relative;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
}
.etc-toast .icon.success {
      border: 4px solid #ffffff;
}
.etc-toast .icon.success:before, .etc-toast .icon.success:after {
        content: '';
        border-radius: 50%;
        position: absolute;
        width: 1.875rem;
        height: 3.75rem;
        background: #4c4c4c;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
}
.etc-toast .icon.success:before {
        border-radius: 10rem 0 0 10rem;
        top: -0.5rem;
        left: -1rem;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        -webkit-transform-origin: 1.875rem 1.875rem;
                transform-origin: 1.875rem 1.875rem;
}
.etc-toast .icon.success:after {
        border-radius: 0 10rem 10rem 0;
        top: -0.34375rem;
        left: 0.9375rem;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        -webkit-transform-origin: 0px 1.875rem;
                transform-origin: 0px 1.875rem;
}
.etc-toast .icon.success .placeholder {
        width: 2.5rem;
        height: 2.5rem;
        border: 4px solid rgba(255, 255, 255, 0.2);
        border-radius: 50%;
        -webkit-box-sizing: content-box;
                box-sizing: content-box;
        position: absolute;
        left: -0.125rem;
        top: -0.125rem;
        z-index: 2;
}
.etc-toast .icon.success .fix {
        width: 0.15625rem;
        height: 2.8125rem;
        background-color: #4c4c4c;
        position: absolute;
        left: 0.875rem;
        top: 0.25rem;
        z-index: 1;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
}
.etc-toast .icon.success .line {
        height: 0.15625rem;
        background-color: white;
        display: block;
        border-radius: 2px;
        position: absolute;
        z-index: 2;
}
.etc-toast .icon.success .line.tip {
          width: 0.78125rem;
          left: 0.4375rem;
          top: 1.4375rem;
          -webkit-transform: rotate(45deg);
                  transform: rotate(45deg);
}
.etc-toast .icon.success .line.long {
          width: 1.46875rem;
          right: 0.25rem;
          top: 1.1875rem;
          -webkit-transform: rotate(-45deg);
                  transform: rotate(-45deg);
}
.etc-toast .icon.error, .etc-toast .icon.info, .etc-toast .icon.warning {
      display: block;
      border: 2px solid #ffffff;
}
.etc-toast .icon.error .icon-text, .etc-toast .icon.info .icon-text, .etc-toast .icon.warning .icon-text {
        font-size: 2rem;
        color: #ffffff;
        line-height: 2.3rem;
        text-align: center;
}
.etc-toast-mask {
    background-color: transparent;
    position: fixed;
    z-index: 2000;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

/*
* Animations
*/
.showAlert {
  -webkit-animation: showAlert 0.3s;
          animation: showAlert 0.3s;
}
@-webkit-keyframes showAlert {
0% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
}
45% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
80% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes showAlert {
0% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
}
45% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
80% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
.hideAlert {
  -webkit-animation: hideAlert 0.2s;
          animation: hideAlert 0.2s;
}
@-webkit-keyframes hideAlert {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
}
@keyframes hideAlert {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
}
@-webkit-keyframes animateSuccessTip {
0% {
    width: 0;
    left: 0.03125rem;
    top: 0.59375rem;
}
54% {
    width: 0;
    left: 0.03125rem;
    top: 0.59375rem;
}
70% {
    width: 1.5625rem;
    left: -0.25rem;
    top: 1.15625rem;
}
84% {
    width: 0.53125rem;
    left: 0.65625rem;
    top: 1.5rem;
}
100% {
    width: 0.78125rem;
    left: 0.4375rem;
    top: 1.40625rem;
}
}
@keyframes animateSuccessTip {
0% {
    width: 0;
    left: 0.03125rem;
    top: 0.59375rem;
}
54% {
    width: 0;
    left: 0.03125rem;
    top: 0.59375rem;
}
70% {
    width: 1.5625rem;
    left: -0.25rem;
    top: 1.15625rem;
}
84% {
    width: 0.53125rem;
    left: 0.65625rem;
    top: 1.5rem;
}
100% {
    width: 0.78125rem;
    left: 0.4375rem;
    top: 1.40625rem;
}
}
@-webkit-keyframes animateSuccessLong {
0% {
    width: 0;
    right: 1.4375rem;
    top: 1.6875rem;
}
65% {
    width: 0;
    right: 1.4375rem;
    top: 1.6875rem;
}
84% {
    width: 1.71875rem;
    right: 0px;
    top: 1.09375rem;
}
100% {
    width: 1.46875rem;
    right: 0.25rem;
    top: 1.1875rem;
}
}
@keyframes animateSuccessLong {
0% {
    width: 0;
    right: 1.4375rem;
    top: 1.6875rem;
}
65% {
    width: 0;
    right: 1.4375rem;
    top: 1.6875rem;
}
84% {
    width: 1.71875rem;
    right: 0px;
    top: 1.09375rem;
}
100% {
    width: 1.46875rem;
    right: 0.25rem;
    top: 1.1875rem;
}
}
@-webkit-keyframes rotatePlaceholder {
0% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
5% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
12% {
    -webkit-transform: rotate(-405deg);
            transform: rotate(-405deg);
}
100% {
    -webkit-transform: rotate(-405deg);
            transform: rotate(-405deg);
}
}
@keyframes rotatePlaceholder {
0% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
5% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
12% {
    -webkit-transform: rotate(-405deg);
            transform: rotate(-405deg);
}
100% {
    -webkit-transform: rotate(-405deg);
            transform: rotate(-405deg);
}
}
.animateSuccessTip {
  -webkit-animation: animateSuccessTip 0.75s;
          animation: animateSuccessTip 0.75s;
}
.animateSuccessLong {
  -webkit-animation: animateSuccessLong 0.75s;
          animation: animateSuccessLong 0.75s;
}
animate::after {
  -webkit-animation: rotatePlaceholder 4.25s ease-in;
          animation: rotatePlaceholder 4.25s ease-in;
}



