.etc-picker-panle {
  width: 100%;
  background: #fff;
  position: fixed;
  bottom: 0;
  z-index: 4000;
  -webkit-transition-duration: 350ms;
          transition-duration: 350ms;
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}
.etc-picker-panle.fade-in {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.etc-picker-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  height: 14rem;
  text-align: center;
  position: relative;
}
.etc-picker-wrapper li {
    height: 2rem;
    line-height: 2rem;
    word-wrap: normal;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    text-align: center;
    font-size: 0.75rem;
    color: #171717;
}
.etc-picker-scroller {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.etc-picker-header {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: .6rem .5rem;
}
.etc-picker-header:after {
    content: '';
    position: absolute;
    z-index: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #e4e4e4;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
}
.etc-picker-header .title {
    text-align: center;
    font-size: 0.75rem;
    color: #666666;
}
.etc-picker-header .btn-left {
    font-size: 0.7rem;
    color: #666666;
}
.etc-picker-header .btn-right {
    font-size: 0.7rem;
    color: #d27e00;
}
.etc-picker-mask {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  z-index: 2000;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.etc-picker .picker-cover {
  position: absolute;
  width: 100%;
  pointer-events: none;
  height: 6rem;
  left: 0;
}
.etc-picker .picker-cover.cover-top {
    top: 0;
    background: -webkit-gradient(linear, left bottom, left top, color-stop(1%, rgba(255, 255, 255, 0.5)), to(rgba(255, 255, 255, 0.99)));
    background: linear-gradient(bottom, rgba(255, 255, 255, 0.5) 1%, rgba(255, 255, 255, 0.99) 100%);
    background: -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0.5) 1%, rgba(255, 255, 255, 0.99) 100%);
}
.etc-picker .picker-cover.cover-top:after {
      content: '';
      position: absolute;
      z-index: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      border-bottom: 1px solid #e4e4e4;
      -webkit-transform: scaleY(0.5);
              transform: scaleY(0.5);
      -webkit-transform-origin: 0 0;
              transform-origin: 0 0;
}
.etc-picker .picker-cover.cover-bottom {
    bottom: 0;
    background: -webkit-gradient(linear, left top, left bottom, from(top), color-stop(1%, rgba(255, 255, 255, 0.5)), to(rgba(255, 255, 255, 0.99)));
    background: linear-gradient(top, rgba(255, 255, 255, 0.5) 1%, rgba(255, 255, 255, 0.99) 100%);
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.5) 1%, rgba(255, 255, 255, 0.99) 100%);
}
.etc-picker .picker-cover.cover-bottom:after {
      content: '';
      position: absolute;
      z-index: 0;
      top: 0;
      left: 0;
      width: 100%;
      border-top: 1px solid #e4e4e4;
      -webkit-transform: scaleY(0.5);
              transform: scaleY(0.5);
      -webkit-transform-origin: 0 0;
              transform-origin: 0 0;
}



