.etc-switch {
  position: relative;
  z-index: 10;
  display: block;
  left: 0;
  border: 1px solid #dfdfdf;
  border-radius: 100rem;
  background-color: #dfdfdf;
  -webkit-appearance: none;
  -moz-appearance: none;
}
.etc-switch:before, .etc-switch:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 100rem;
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}
.etc-switch:before {
    background-color: #ffffff;
}
.etc-switch:after {
    background-color: #ffffff;
    -webkit-box-shadow: 0 0.05rem 0.15rem rgba(0, 0, 0, 0.4);
            box-shadow: 0 0.05rem 0.15rem rgba(0, 0, 0, 0.4);
}
.etc-switch:checked {
    border-color: currentColor;
    background-color: currentColor;
}
.etc-switch[disabled] {
    opacity: 0.5;
}
.etc-switch-size-md {
    width: 2.6rem;
    height: 1.6rem;
}
.etc-switch-size-md:before, .etc-switch-size-md:after {
      height: 1.5rem;
      content: "";
}
.etc-switch-size-md:before {
      width: 2.5rem;
      content: "";
}
.etc-switch-size-md:after {
      width: 1.5rem;
}
.etc-switch-size-md:checked:before {
      -webkit-transform: scale(0);
              transform: scale(0);
}
.etc-switch-size-md:checked:after {
      -webkit-transform: translateX(1rem);
              transform: translateX(1rem);
}
.etc-switch-size-sm {
    width: 1.82rem;
    height: 1.12rem;
}
.etc-switch-size-sm:before, .etc-switch-size-sm:after {
      height: 1.02rem;
      content: "";
}
.etc-switch-size-sm:before {
      width: 1.72rem;
      content: "";
}
.etc-switch-size-sm:after {
      width: 1.02rem;
}
.etc-switch-size-sm:checked:before {
      -webkit-transform: scale(0);
              transform: scale(0);
}
.etc-switch-size-sm:checked:after {
      -webkit-transform: translateX(0.7rem);
              transform: translateX(0.7rem);
}
.etc-switch-size-lg {
    width: 3.12rem;
    height: 1.92rem;
}
.etc-switch-size-lg:before, .etc-switch-size-lg:after {
      height: 1.82rem;
      content: "";
}
.etc-switch-size-lg:before {
      width: 3.02rem;
      content: "";
}
.etc-switch-size-lg:after {
      width: 1.82rem;
}
.etc-switch-size-lg:checked:before {
      -webkit-transform: scale(0);
              transform: scale(0);
}
.etc-switch-size-lg:checked:after {
      -webkit-transform: translateX(1.2rem);
              transform: translateX(1.2rem);
}



