.etc-button {
  display: block;
  height: 2.2rem;
  border-radius: 0.2rem;
  text-align: center;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 0;
  font-size: 0.8rem;
}
.etc-button.btn-default {
    border: 1px solid #eabf4f;
    color: #d27e00;
    background: none;
    background-color: #ffffff;
}
.etc-button.btn-default:active {
      background-color: #e6e6e6;
}
.etc-button.btn-primary {
    color: #8e4d00;
    background-color: #eabf4f;
}
.etc-button.btn-primary:active {
      background-color: #d3ac47;
}
.etc-button.btn-danger {
    color: #ffffff;
    background-color: #E64340;
}
.etc-button.btn-danger:active {
      background-color: #cf3c3a;
}
.etc-button.btn-disable {
    background: #999999;
    color: #ffffff;
}
.etc-button.size-md {
    border-radius: 0.2rem;
    text-align: center;
    height: 1.75rem;
}
.etc-button.size-sm {
    height: 1.5rem;
    border-radius: 0.2rem;
    text-align: center;
    width: 3rem;
    font-size: 0.65rem;
}



