.etc-alert {
  background-color: white;
  width: 15rem;
  border-radius: 4px;
  text-align: center;
  position: fixed;
  left: 50%;
  top: 50%;
  margin-left: -7.5rem;
  margin-top: -6.25rem;
  overflow: hidden;
  z-index: 4000;
}
.etc-alert-mask {
    background-color: rgba(0, 0, 0, 0.6);
    position: fixed;
    z-index: 2000;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.etc-alert-title {
    font-size: 0.8rem;
    color: #333;
    padding-top: .5rem;
}
.etc-alert-content {
    font-size: 0.75rem;
    color: #666666;
    padding: 1.5rem 0.6rem;
}
.etc-alert .alert-button {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    line-height: 2.2rem;
}
.etc-alert .alert-button:after {
      content: '';
      position: absolute;
      z-index: 0;
      top: 0;
      left: 0;
      width: 100%;
      border-top: 1px solid #e4e4e4;
      -webkit-transform: scaleY(0.5);
              transform: scaleY(0.5);
      -webkit-transform-origin: 0 0;
              transform-origin: 0 0;
}
.etc-alert .alert-button div {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      position: relative;
}
.etc-alert .alert-button div:first-child:after {
        content: '';
        position: absolute;
        z-index: 0;
        top: 0;
        right: 0;
        height: 100%;
        border-right: 1px solid #e4e4e4;
        -webkit-transform: scaleX(0.5);
                transform: scaleX(0.5);
        -webkit-transform-origin: 0 0;
                transform-origin: 0 0;
}
.etc-alert .alert-button-left {
      color: #666666;
}
.etc-alert .alert-button-right {
      color: #d27e00;
}

/*
* Animations
*/
.showAlert {
  -webkit-animation: showAlert 0.3s;
          animation: showAlert 0.3s;
}
@-webkit-keyframes showAlert {
0% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
}
45% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
80% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes showAlert {
0% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
}
45% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
80% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
.hideAlert {
  -webkit-animation: hideAlert 0.2s;
          animation: hideAlert 0.2s;
}
@-webkit-keyframes hideAlert {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
}
@keyframes hideAlert {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
}



