.etc-item-block {
  background: #ffffff;
}
.etc-item-block:not(:first-child) {
    margin-top: 0.5rem;
}
.etc-item-block a.item,
  .etc-item-block lable.item {
    background-color: #ffffff;
}
.etc-item-block a.item:active,
    .etc-item-block lable.item:active {
      background-color: whitesmoke;
}
.etc-item-block .item {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 2.5rem;
    padding: 0.85rem 0.75rem;
}
.etc-item-block .item:not(:last-child):after {
      content: '';
      position: absolute;
      z-index: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      border-bottom: 1px solid #e4e4e4;
      -webkit-transform: scaleY(0.5);
              transform: scaleY(0.5);
      -webkit-transform-origin: 0 0;
              transform-origin: 0 0;
}
.etc-item-block .item-title {
      font-size: 0.75rem;
      color: #333;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: start;
          -ms-flex-pack: start;
              justify-content: flex-start;
}
.etc-item-block .item-title .icon {
        width: 1rem;
        height: 1rem;
        fill: #a6a5a5;
        overflow: hidden;
        vertical-align: -0.25rem;
