.etc-radio-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.etc-radio {
  position: relative;
  z-index: 10;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  left: 0;
  border: 1px solid #c9c9c9;
  background-color: #ffffff;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  -webkit-transition: all 200ms cubic-bezier(0.4, 0, 0.23, 1);
  transition: all 200ms cubic-bezier(0.4, 0, 0.23, 1);
  width: 1rem;
  height: 1rem;
  border-radius: 10rem;
}
.etc-radio[disabled] {
    opacity: 0.5;
}
.etc-radio.circled:checked:after {
    top: 0;
    border: 0.2rem solid #fff;
    border-radius: 1rem;
    background: currentColor;
    -webkit-animation: radiocheck 250ms 80ms cubic-bezier(0.1, 0, 0.1, 1) forwards;
            animation: radiocheck 250ms 80ms cubic-bezier(0.1, 0, 0.1, 1) forwards;
}
.etc-radio.type-checkbox:checked {
    background-color: currentColor;
}
.etc-radio.type-checkbox:checked:after {
      top: -3px;
      border-right: 0.075rem solid transparent;
      border-bottom: 0.075rem solid transparent;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
      -webkit-animation: checkboxcheck 125ms 50ms cubic-bezier(0.1, 0, 0.1, 1) forwards;
              animation: checkboxcheck 125ms 50ms cubic-bezier(0.1, 0, 0.1, 1) forwards;
}
.etc-radio-label {
    padding-left: 0.3rem;
    font-size: 0.75rem;
}
.etc-radio:checked {
  border-color: currentColor;
  -webkit-animation: leave 200ms cubic-bezier(0.4, 0, 0.5, 1);
          animation: leave 200ms cubic-bezier(0.4, 0, 0.5, 1);
}
.etc-radio:checked:after {
    content: "";
    margin: auto;
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
}
@-webkit-keyframes radiocheck {
0% {
    width: 0;
    height: 0;
}
33% {
    width: 0.4rem;
    height: 0.4rem;
}
100% {
    width: 0.855rem;
    height: 0.855rem;
}
}
@keyframes radiocheck {
0% {
    width: 0;
    height: 0;
}
33% {
    width: 0.4rem;
    height: 0.4rem;
}
100% {
    width: 0.855rem;
    height: 0.855rem;
}
}
@-webkit-keyframes checkboxcheck {
0% {
    width: 0;
    height: 0;
    border-color: white;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
33% {
    width: 0.2rem;
    height: 0.4rem;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
100% {
    width: 0.3rem;
    height: 0.6rem;
    border-color: white;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
}
@keyframes checkboxcheck {
0% {
    width: 0;
    height: 0;
    border-color: white;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
33% {
    width: 0.2rem;
    height: 0.4rem;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
100% {
    width: 0.3rem;
    height: 0.6rem;
    border-color: white;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
}
@-webkit-keyframes leave {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
33% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes leave {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
33% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}



