.etc-grid-2, .etc-grid-3, .etc-grid-4, .etc-grid-5 {
  position: relative;
  float: left;
  text-align: center;
}
.etc-grid-2:nth-child(n+3), .etc-grid-3:nth-child(n+4), .etc-grid-4:nth-child(n+5), .etc-grid-5:nth-child(n+6) {
