.etc-tab-nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  z-index: 0;
  background: white;
}
.etc-tab-nav:after {
    content: '';
    position: absolute;
    z-index: 3;
    bottom: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #e4e4e4;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
}
.etc-tab-nav-item {
    width: 1%;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    position: relative;
    text-align: center;
    display: block;
    line-height: 2rem;
}
.etc-tab-nav-item a {
      display: inherit;
      color: inherit;
}
.etc-tab-nav-item:not(:last-child):after {
      position: absolute;
      top: 35%;
      right: 0;
      content: "";
      width: 1px;
      height: 30%;
      -webkit-transform: scaleX(0.5);
              transform: scaleX(0.5);
      border-right: 1px solid #e4e4e4;
}
.etc-tab-nav .etc-tab-active:before {
    content: "";
    width: 70%;
    height: 2px;
    position: absolute;
    left: 50%;
    bottom: 0;
    margin-left: -35%;
    z-index: 4;
    background-color: currentColor;
}
.etc-tab-panel {
  position: relative;
  overflow: hidden;
  background-color: #fff;
}
.etc-tab-panel-item {
    width: 100%;
    position: absolute;
    top: 0;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
.etc-tab-panel-item.etc-tab-active {
      position: relative;
      -webkit-transition: -webkit-transform 0.15s;
      transition: -webkit-transform 0.15s;
      transition: transform 0.15s;
      transition: transform 0.15s, -webkit-transform 0.15s;
      -webkit-transform: translateX(0);
              transform: translateX(0);
}
.etc-tab-panel-item.etc-tab-active ~ .etc-tab-panel-item {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
}



