.etc-actionsheet-panle {
  width: 100%;
  background: #f5f5f5;
  position: fixed;
  bottom: 0;
  z-index: 4000;
  -webkit-transition-duration: 350ms;
          transition-duration: 350ms;
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}
.etc-actionsheet-panle.fade-in {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.etc-actionsheet-item {
  background: #ffffff;
  position: relative;
  line-height: 2.5rem;
  text-align: center;
  font-size: 0.8rem;
  color: #333;
  background-color: #ffffff;
}
.etc-actionsheet-item:active {
    background-color: #e6e6e6;
}
.etc-actionsheet-item:after {
    content: '';
    position: absolute;
    z-index: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #e4e4e4;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
}
.etc-actionsheet-item.item-bottom {
    margin-top: .5rem;
    color: #E64340;
}
.etc-actionsheet-mask {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  z-index: 2000;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}



